import axios from "axios"
import qs from "qs"


// ,,,retur n
axios.interceptors.request.use(config => {
  console.log("=================");
  console.log(config);
  console.log("======================");

  // 
  var status = sessionStorage.getItem("status")
  if (status) {
    config.status = status
  }
  return config
})



// return
axios.interceptors.response.use((res) => {
  console.log("=================");
  console.log(res);
  console.log("======================");
  if (!res.data.isok) {
    alert("")
  }
  return res
})

// 
// const baseUrl = "/api"
// 
const baseUrl = ""

// 
export const login = (user) => {
  // user:{name:this.name,pass:this.pass}

  // 
  return axios({
    url: baseUrl+"/login",
    method: "post",
    data: qs.stringify(user)
  })

  // 
  // var form = new FormData()
  // for (let i in user) {
  //   form.append(i, user[i])
  // }
  // return axios({
  //   url: "/api/login",
  //   method: "post",
  //   data: form
  // })
}

// 
export const food = () => {
  return axios({
    url: baseUrl+"/foodList",
    method: "get"
  })
}

// 
export const foodDetail = id => {
  return axios({
    url: baseUrl+"/getFoodDetail",
    method: "get",
    params: {
      foodId: id
    }
  })
}

// 
export const movie = () => {
  return axios({
    url: baseUrl+"/movie",
    method: "get"
  })
}

// 
export const movieDetail = id => {
  return axios({
    url: baseUrl+"/getMovieDetail",
    method: "get",
    params: {
      id: id
    }
  })
}



